@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,500&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}


.app {
  text-align: center;
  max-width: 950px;
  margin: auto;
  font-size: 18px;
  font-weight: 300;
  color: #252525; }

.navbar {
  display: flex;
  flex-direction: row;
  padding: 32px 0; }
  @media only screen and (max-width: 500px) {
    .navbar {
      padding: 32px 7%; } }
  .navbar .left .logoLink {
    position: relative;
    display: flex; }
  .navbar .left .voyage, .navbar .left .labs {
    display: inline-block;
    vertical-align: middle;
    margin: auto;
    font-weight: 300;
    font-size: 36px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    letter-spacing: 1.1px;
    top: 0; }
    @media only screen and (max-width: 500px) {
      .navbar .left .voyage, .navbar .left .labs {
        font-size: 30px;
        margin-top: 20px; } }
  .navbar .left .voyage {
    color: #252525; }
  .navbar .left .labs {
    color: #919191;
    margin-left: 10px; }
  .navbar .left .logo {
    position: relative;
    left: 0px;
    width: auto;
    height: 50px;
    margin-right: 15px; }
    @media only screen and (max-width: 500px) {
      .navbar .left .logo {
        height: 40px;
        margin-top: 20px;
        margin-left: 30px; } }
  .navbar .left .logo:hover {
    -webkit-filter: brightness(95%);
            filter: brightness(95%); }
  .navbar .right {
    margin-left: auto; }

.button {
  background-color: #2B009E;
  padding: 10px 19px;
  border-radius: 25px;
  height: 50px;
  outline: none;
  border: none;
  color: #FFFFFF;
  display: inline-block;
  cursor: pointer;
  display: flex;
  font-size: 15px;
  font-weight: 500;
  width: 100%; }
  .button:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%); }
  .button:focus {
    -webkit-filter: brightness(85%);
            filter: brightness(85%); }
  .button .buttonChildren {
    margin: auto;
    display: inline-block;
    vertical-align: baseline; }

.contactModal {
  width: 630px;
  max-width: 80%;
  background-color: #FFFFFF;
  padding: 50px;
  outline: none;
  border-radius: 10px;
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 100px;
  overflow-y: auto;
  position: relative;
  margin-top: 90px; }
  .contactModal .contactHeader {
    font-family: 'Rubik';
    font-weight: 400;
    color: #2B009E;
    font-size: 40px;
    margin: auto 0 16px 0;
    line-height: 1.0; }
  .contactModal .formTitle {
    font-family: 'Rubik';
    font-weight: 300;
    font-size: 15px;
    line-height: 2.0;
    font-weight: 400; }
  .contactModal .inputField, .contactModal .textarea {
    width: 100%;
    border: none;
    border-bottom: 1px solid #2B009E;
    outline: none;
    margin-bottom: 20px;
    font-family: 'Rubik';
    font-weight: 300;
    font-size: 15px;
    line-height: 2.0;
    color: #252525; }
    .contactModal .inputField:focus, .contactModal .textarea:focus {
      background-color: rgba(43, 0, 158, 0.02);
      box-shadow: 0 5px 12px -12px #2B009E; }
  .contactModal .textarea {
    resize: none; }
  @media only screen and (max-width: 500px) {
    .contactModal {
      margin: auto;
      margin-right: 50px;
      width: 400px; } }

.modalOverlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 100; }

.main {
  display: flex;
  min-width: 450px;
  margin: 50px 0 100px 0;
  font-family: 'Rubik';
  font-weight: 300;
  font-size: 15px;
  line-height: 2.0;
  flex-direction: row; }
  @media only screen and (max-width: 500px) {
    .main {
      display: block;
      margin-left: 50px; } }
  .main .left {
    margin-top: 120px;
    text-align: left;
    width: 55%;
    padding-right: 10px; }
  .main .right {
    width: 40%;
    margin-left: 50px;
    position: relative; }
  .main .title {
    font-family: 'Rubik';
    font-weight: 400;
    color: #2B009E;
    font-size: 40px;
    margin: auto 0 16px 0;
    line-height: 1.0; }
  .main .bodytext {
    margin-bottom: 16px; }
  .main .contact {
    display: inline-block;
    margin-top: 10px;
    z-index: 4; }
  .main .sensor1, .main .sensor2, .main .sensor3 {
    position: absolute;
    left: 0;
    -webkit-transition: 1.2s all;
    transition: 1.2s all;
    width: 100%; }
    .main .sensor1:hover, .main .sensor2:hover, .main .sensor3:hover {
      -webkit-transform: translateY(-55px);
              transform: translateY(-55px);
      -webkit-transition: 0.7s all;
      transition: 0.7s all; }
  .main .sensor1 {
    z-index: 3; }
  .main .sensor2 {
    z-index: 2;
    top: 125px; }
  .main .sensor3 {
    z-index: 1;
    top: 250px; }


